"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import api from "@/lib/api";
import Link from "next/link";

const AdminCreateCoursePage = () => {
    const router = useRouter();
    const [title, setTitle] = useState("");
    const [loading, setLoading] = useState(false);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setLoading(true);

        try {
            const { data } = await api.post("/courses", { title });
            // Redirect to the admin course edit page
            router.push(`/admin/courses/${data.id}`);
        } catch (error) {
            console.error("Failed to create course", error);
            alert("Failed to create course. Check console for details.");
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="container mx-auto p-6 max-w-2xl min-h-screen d-flex flex-column justify-content-center">
            <div className="mb-4">
                <h1 className="text-2xl font-bold mb-3">Name your course</h1>
                <p className="text-muted mb-4" style={{ fontSize: '1.1rem' }}>
                    What would you like to name your course? Don't worry, you can change this later.
                </p>
            </div>

            <form onSubmit={handleSubmit} className="space-y-6">
                <div className="mb-4">
                    <label className="form-label fw-medium mb-2">
                        Course Title
                    </label>
                    <input
                        type="text"
                        className="form-control form-control-lg p-3 border rounded-md shadow-sm"
                        placeholder="e.g. Advanced Web Development"
                        value={title}
                        onChange={(e) => setTitle(e.target.value)}
                        disabled={loading}
                        required
                    />
                </div>

                <div className="d-flex align-items-center gap-2">
                    <Link href="/admin/courses" className="btn btn-outline-secondary px-4 py-2">
                        Cancel
                    </Link>
                    <button
                        type="submit"
                        disabled={!title || loading}
                        className="btn btn-primary px-4 py-2"
                    >
                        {loading ? "Creating..." : "Continue"}
                    </button>
                </div>
            </form>
        </div>
    );
};

export default AdminCreateCoursePage;
